/** @jsx jsx */

import React, { useEffect, useState } from "react";
import { jsx, css } from "@emotion/core";
import styled from "@emotion/styled/macro";
import SideNavBar from "./SideNavBar/SideNavBar";
import NameCard from "./NameCard/NameCard";
import MainPage from "./MainPage/MainSection";
import { Row, Col } from "./theme/styled";
import MobileNavBar from "./MobileNavBar";

const Container = styled.div`
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  @media (min-width: 768px) {
    width: 750px;
  }

  @media (min-width: 992px) {
    width: 970px;
  }

  @media (min-width: 1200px) {
    width: 1170px;
  }

  @media (max-width: 767px) {
    top: 15px;
  }

  width: 100%;
  max-width: 860px;
  position: relative;
  padding-bottom: 15px;
`;

const Content = styled.div`
  /* Row System */
  ${Row}
  /* Custom */
  position: relative;
  min-height: 620px;
`;

const LeftColumn = styled.div`
  ${Col}
  @media (min-width: 768px) {
    width: 33.33333333%;
  }
`;
const RightColumn = styled.div`
  ${Col}
  @media (min-width: 768px) {
    width: 66.66666667%;
  }

  padding-left: 0px;
`;

export interface VCardProps {}

const VCard: React.SFC<VCardProps> = () => {
  const [height, setHeight] = useState(window.innerHeight / 5);

  const setHeightListener = () => {
    setHeight(window.innerHeight / 5);
    if (window.innerHeight < 700)
      return setHeight(window.innerHeight / 5 - 100);
    if (window.innerHeight < 800) return setHeight(window.innerHeight / 5 - 50);
  };
  useEffect(() => {
    window.addEventListener("resize", setHeightListener);

    return () => {
      window.removeEventListener("resize", setHeightListener);
    };
  }, []);
  return (
    <Container
      css={css`
        top: ${`${height}px`};
      `}
    >
      <Content>
        <LeftColumn>
          <NameCard />
          <SideNavBar />
        </LeftColumn>
        <RightColumn>
          <MainPage />
          <MobileNavBar />
        </RightColumn>
      </Content>
    </Container>
  );
};

export default VCard;
