/** @jsx jsx */

import React from "react";
import { jsx } from "@emotion/core";
import FontAwesomeIcon from "../FontAwesomeIcon";
import { Link } from "react-router-dom";
import Styled from "@emotion/styled/macro";
import styled, { ActiveStyle } from "../theme/styled";

const HoverBackground = Styled.div`
  position: absolute;
  background-color: #111111;
  z-index: -1;
  width: 300%;
  height: 300%;
  top: 200%;
  left: -100%;
  -ms-transform: rotate(15deg); /* IE 9 */
  -webkit-transform: rotate(15deg); /* Safari */
  -moz-transform: rotate(15deg); /* Safari */
  transform: rotate(15deg);
  -webkit-transition: top 0.5s, left 0.5s; /* Safari */
  -moz-transition: top 0.5s, left 0.5s; /* Safari */
  -o-transition: top 0.5s, left 0.5s; /* Safari */
  transition-timing-function: ease-out;
  transition: top 0.5s, left 0.5s;
`;

const NavItem = styled.li`
  position: relative;
  padding: 12px 15px;
  width: 100%;
  color: ${({ theme }) => theme.colors.contrast};
  border-bottom: 1px solid ${({ theme }) => theme.colors.border};
  font-size: 16px;
  margin: 0px;
  font-family: "Oswald", sans-serif;
  line-height: 1.4;
  cursor: pointer;
  overflow: hidden;

  :hover {
    border-bottom-color: ${({ theme }) => theme.colors.contrast};
    color: ${({ theme }) => theme.colors.primary};

    ${HoverBackground} {
      top: 0%;
      left: -15%;
    }
  }

  @media (max-width: 767px) {
    width: 25%;
    margin: 0px;
    padding: 15px;
    font-size: 19px;
    float: left;
    text-align: center;
    border-right: 1px solid ${({ theme }) => theme.colors.border};
    border-bottom: none;
  }
`;
const NavLinkWrapper = styled(Link)`
  :hover {
    text-decoration: none;
  }
`;

const MenuItemName = styled.span`
  padding-right: 25px;
  border-right: 2px solid ${({ theme }) => theme.colors.border};
  margin-right: 15px;
  text-transform: uppercase;

  @media (max-width: 767px) {
    display: none;
  }
`;

export interface SideNavBarItemProps {
  itemName: string;
  iconName: string;
  to: string;
  active?: boolean;
}

const SideNavBarItem: React.SFC<SideNavBarItemProps> = ({
  itemName,
  iconName,
  to,
  active,
}) => {
  return (
    <NavLinkWrapper to={to}>
      <NavItem css={active && ActiveStyle}>
        <HoverBackground />
        <MenuItemName>{itemName}</MenuItemName>
        <FontAwesomeIcon name={iconName} />
      </NavItem>
    </NavLinkWrapper>
  );
};

export default SideNavBarItem;
