import React, { FormEvent, useState } from "react";
import ContainerSection from "./ContainerSection";
import styled from "../theme/styled";
import ContactInput from "./ContactSection/ContactInput";
import SnackBar from "./ContactSection/Snackbar";

const Form = styled.form`
  width: 100%;
  max-width: 480px;
  margin: 2rem auto 0;
  /* text-align: center; */
`;

const Title = styled.h3`
  text-transform: uppercase;
  color: ${({ theme }) => theme.colors.border};
`;

const SubmitButton = styled.button`
  height: 3em;
  width: 30%;
  padding: 1.5em auto;
  margin: 1rem 0.5rem;
  background-color: ${({ theme }) => theme.colors.darkSecondary};
  color: ${({ theme }) => theme.colors.border};
  font-size: 15px;
  font-weight: 700;
  border: none;
  border-radius: 3px;
  text-transform: uppercase;
  letter-spacing: 0.5em;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);

  :hover {
    letter-spacing: 0.8em;
    background-color: ${({ theme }) => theme.colors.primary};
  }

  :focus {
    outline: none;
  }
`;

export interface ContactProps {}

const ContactSection: React.SFC<ContactProps> = () => {
  const [sendMessage, setSendMessage] = useState(false);
  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setSendMessage(true);
  };

  return (
    <ContainerSection>
      <Title>DON'T FORGET TO MAIL ME</Title>
      <Form onSubmit={handleSubmit}>
        <ContactInput
          label="Name"
          type="text"
          placeHolder="What's your name?"
        />
        <ContactInput
          label="Email"
          type="email"
          placeHolder="What's your email?"
        />
        <ContactInput
          label="Message"
          textArea
          type="text"
          placeHolder="Told me your story :)"
        />
        <SubmitButton>Send</SubmitButton>
      </Form>
      {sendMessage && <SnackBar />}
    </ContainerSection>
  );
};

export default ContactSection;
