/** @jsx jsx */

import React from "react";
import { jsx, css } from "@emotion/core";
import styled from "@emotion/styled/macro";

import FontAwesomeIcon from "../FontAwesomeIcon";

const SocialMenuItemLink = styled.a`
  color: #111111;
`;

const SocialMenuItem = styled.li`
  float: left;
  width: 40px;
  height: 40px;
  text-align: center;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.1s ease-in;
  -webkit-transition: all 0.1s ease-in;
  :hover {
    background-color: #111111;
    color: #f4d03f;

    ${SocialMenuItemLink} {
      color: #f4d03f;
    }
  }
`;

const centerIcon = css`
  margin-top: 33%;
`;

export interface SideNavBarSocialMenuItemProps {
  linkTo: string;
  itemIconName: string;
}

const SideNavBarSocialMenuItem: React.SFC<SideNavBarSocialMenuItemProps> = ({
  linkTo,
  itemIconName,
}) => {
  return (
    <SocialMenuItemLink href={linkTo}>
      <SocialMenuItem>
        <FontAwesomeIcon name={itemIconName} customCss={[centerIcon]} />
      </SocialMenuItem>
    </SocialMenuItemLink>
  );
};

export default SideNavBarSocialMenuItem;
