/** @jsx jsx */

import React from "react";
import { jsx } from "@emotion/core";
import styled from "@emotion/styled/macro";
import { Store } from "../../reducers";
import { SideNavbarSocialMenuItem } from "../../reducers/NonPersistentUIReducer";
import { connect } from "react-redux";
import SideNavBarSocialMenuItem from "./SideNavBarSocialMenuItem";
import { noPadding } from "../theme/styled";

const Container = styled.div`
  position: absolute;
  bottom: 0px;
  right: 0px;
  left: 0;
  text-align: center;

  @media (max-width: 767px) {
    display: none;
  }
`;

const SocialMenu = styled.ul`
  display: inline-block;
  float: none;
  font-size: 18px;
  margin-right: 5px;
  margin-bottom: 10px;
`;

export interface SideNavBarSocialMenuProps {
  socialMenuItems: SideNavbarSocialMenuItem[];
}

const SideNavBarSocialMenu: React.SFC<SideNavBarSocialMenuProps> = ({
  socialMenuItems,
}) => {
  return (
    <Container>
      <SocialMenu css={noPadding}>
        {socialMenuItems.map((socialItem) => (
          <SideNavBarSocialMenuItem
            key={socialItem.linkTo}
            linkTo={socialItem.linkTo}
            itemIconName={socialItem.itemIconName}
          />
        ))}
      </SocialMenu>
    </Container>
  );
};

const mapStateToProps = (state: Store) => ({
  socialMenuItems: state.nonPersistentUIStore.sideNavbarSocialMenuItems,
});

export default connect(mapStateToProps)(SideNavBarSocialMenu);
