import * as React from "react";
import { useRef, useEffect } from "react";
import { createPortal } from "react-dom";
import styled from "../../theme/styled";
import { Project } from "../../../reducers/PortfolioInformationReducer";

const ModalContainer = styled.div`
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalContent = styled.div`
  border-radius: 3px;
  border: 8px solid ${({ theme }) => theme.colors.primary};
  box-shadow: 0 0px 7px rgb(0, 0, 0);
  width: 600px;
  margin: 30px auto;
  background-color: #ffffff;
`;

const Header = styled.div`
  background-color: ${({ theme }) => theme.colors.background};
  border-bottom: none;
  padding: 13px 20px 22px;
`;

const Body = styled.div`
  padding: 20px;
`;

const Footer = styled.div`
  padding: 20px;
  border-top: 0;
  padding-top: 0;
`;

const CloseButton = styled.button`
  background: rgba(255, 0, 0, 0.83);
  opacity: 1;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  margin-top: 3px;
  color: #fff;
  text-shadow: none;
  float: right;
  font-size: 21px;
  font-weight: 700;
  line-height: 1;

  :hover {
    color: ${({ theme }) => theme.colors.contrast};
  }
`;

const Title = styled.h4`
  display: inline-block;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  margin-bottom: -10px;
  color: #fff;
  font-weight: 500;
`;

const Image = styled.img`
  display: block;
  max-width: 100%;
  height: auto;
`;

const Description = styled.div`
  text-align: left;
`;

export interface ModalProps {
  project: Project;
  closeModal: () => void;
}

const modalRoot = document.getElementById("modal");

const Modal: React.SFC<ModalProps> = ({ project, closeModal }) => {
  const elRef = useRef(document.createElement("div"));

  useEffect(() => {
    if (!modalRoot) {
      return;
    }
    modalRoot.appendChild(elRef.current);

    return () => {
      modalRoot.removeChild(elRef.current);
    };
  }, []);

  return createPortal(
    <ModalContainer>
      <ModalContent>
        <Header>
          <CloseButton onClick={closeModal}>
            <span></span>
          </CloseButton>
          <Title>{project.name}</Title>
        </Header>
        <Body>
          <Image src={project.imageSrc} alt="modal-img" />
        </Body>
        <Footer>
          <Description>
            <p>{project.description}</p>
          </Description>
        </Footer>
      </ModalContent>
    </ModalContainer>,
    elRef.current
  );
};

export default Modal;
