import * as React from "react";
import styled, { noPadding } from "./theme/styled";
import { Store } from "../reducers";
import { connect } from "react-redux";
import { SideNavbarSocialMenuItem } from "../reducers/NonPersistentUIReducer";
import FontAwesomeIcon from "./FontAwesomeIcon";

const Container = styled.div`
  background-color: #666;
  margin: 15px 0px;
  margin-left: 8px;
  display: none;
  width: 100%;

  @media (max-width: 767px) {
    display: inline-block;
  }
`;

const List = styled.ul`
  width: auto;
  ${noPadding}
  float: right;
  color: ${({ theme }) => theme.colors.primary};
  font-size: 15px;
  margin-bottom: 0px;
  @media (max-width: 767px) {
    float: none;
  }
`;

const Item = styled.li`
  float: left;
  margin: 10px;
`;

const WrapperLink = styled.a`
  color: ${({ theme }) => theme.colors.darkSecondary};
`;

export interface MobileNavBarProps {
  socialItems: SideNavbarSocialMenuItem[];
}

const MobileNavBar: React.SFC<MobileNavBarProps> = ({ socialItems }) => {
  return (
    <Container>
      <List>
        <Item>Connect With Me</Item>
        {socialItems.map((item) => (
          <WrapperLink href={item.linkTo} key={item.itemIconName}>
            <Item>
              <FontAwesomeIcon name={item.itemIconName} />
            </Item>
          </WrapperLink>
        ))}
      </List>
    </Container>
  );
};

const mapStateToProps = (state: Store) => ({
  socialItems: state.nonPersistentUIStore.sideNavbarSocialMenuItems,
});

export default connect(mapStateToProps)(MobileNavBar);
