import React from "react";
import styled from "@emotion/styled/macro";
import { Row, Col } from "../../theme/styled";
import Bar from "./Bar";

const StyledSkill = styled.li`
  ${Row}
  height:20px;
  margin-bottom: 10px;
`;

const SkillName = styled.div`
  ${Col}
  float: left;
  width: 16.66666667%;
`;

const SkillBar = styled.div`
  ${Col}
  padding-top:3px;
  width: 75%;
  float: left;
`;

export interface SkillProps {
  name: string;
  width: string;
}

const MainSkill: React.SFC<SkillProps> = ({ name, width }) => {
  return (
    <StyledSkill>
      <SkillName>{name}</SkillName>
      <SkillBar>
        <Bar width={width} />
      </SkillBar>
    </StyledSkill>
  );
};

export default MainSkill;
