/** @jsx jsx */

import React from "react";
import { jsx, SerializedStyles } from "@emotion/core";
import styled from "@emotion/styled/macro";

import TypedText from "./TypedText";
import { WhiteText } from "./TitleName";
import { getRandomId } from "./utils/utils";

export const H3 = styled.h3`
  line-height: 1.1;
  position: relative;
  margin-bottom: 20px;
`;

export interface TitleRoleProps {
  roles: string[];
  restCss: SerializedStyles[];
}

const TitleRole: React.SFC<TitleRoleProps> = ({ roles, restCss }) => {
  return (
    <H3 css={[...restCss]}>
      <WhiteText>
        <TypedText typedText={roles} typedId={getRandomId()} />
      </WhiteText>
    </H3>
  );
};

export default TitleRole;
