/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import Typed from "typed.js";

export interface TypedTextProps {
  typedId: string;
  typedText: string[];
}

const TypedText: React.SFC<TypedTextProps> = ({ typedId, typedText }) => {
  useEffect(() => {
    const typed = new Typed(`#${typedId}`, {
      strings: typedText,
      typeSpeed: 45,
      backSpeed: 35,
      loop: true,
      backDelay: 2500,
    });

    return () => {
      typed.destroy();
    };
  }, []);

  return <span id={typedId} />;
};

export default TypedText;
