/* eslint-disable react-hooks/exhaustive-deps */
//Core modules
import React, { useEffect, Dispatch } from "react";
import styled from "@emotion/styled";
import { ThemeProvider } from "emotion-theming";
import { connect } from "react-redux";
import { AnimatePresence, motion } from "framer-motion";

//components
import FrontPage from "./components/FrontPage";

// Image
import mainBg from "./images/main-bg.jpg";
import LoadingScreen from "./components/LoadingScreen";
import { Store } from "./reducers";
import setPreLoadingActionCreator from "./actionCreators/setPreLoadingActionCreator";
import { NonPersistentUIStore } from "./reducers/NonPersistentUIReducer";
import VCard from "./components/VCard";
import FadeAnimateWrapper from "./components/FadeAnimateWrapper";
import { theme } from "./components/theme/styled";

const MainApp = styled.div`
  background: url(${mainBg}) center bottom fixed;
  background-size: cover;
  background-color: #e8e8e8;
  margin: 0px;
  padding: 0px;
  font-size: 14px;
  color: #111;
  width: 100%;
  height: 100%;
  overflow: hidden;
  min-height: 100vh;
  background-repeat: no-repeat;
`;

export interface AppProps {
  nonPersistentUIStore: NonPersistentUIStore;
  setPreLoadingState: (loading: boolean) => void;
}

const App: React.SFC<AppProps> = ({
  nonPersistentUIStore,
  setPreLoadingState,
}) => {
  const { displayPortfolio, preLoading } = nonPersistentUIStore;

  useEffect(() => {
    const loadingTimer = setTimeout(() => setPreLoadingState(false), 1100);

    return () => {
      clearTimeout(loadingTimer);
    };
  }, []);
  return (
    <AnimatePresence>
      <ThemeProvider theme={theme}>
        {preLoading ? (
          <FadeAnimateWrapper key="loadingScreen">
            <LoadingScreen />
          </FadeAnimateWrapper>
        ) : (
          <MainApp>
            <FadeAnimateWrapper key="loadingPage">
              <AnimatePresence>
                {displayPortfolio ? (
                  <motion.div
                    key="vCard"
                    initial={{ scale: 0 }}
                    animate={{
                      scale: 1,
                      transition: { duration: 0.5, delay: 0.53 },
                    }}
                  >
                    <VCard />
                  </motion.div>
                ) : (
                  <motion.div
                    key="frontPage"
                    exit={{
                      scale: 0,
                      y: 200,
                      transition: {
                        duration: 0.5,
                      },
                    }}
                  >
                    <FrontPage />
                  </motion.div>
                )}
              </AnimatePresence>
            </FadeAnimateWrapper>
          </MainApp>
        )}
      </ThemeProvider>
    </AnimatePresence>
  );
};

const mapStateToProps = (state: Store) => ({
  nonPersistentUIStore: state.nonPersistentUIStore,
});

const mapDispatchToProps = (dispatch: Dispatch<any>) => ({
  setPreLoadingState: (loading: boolean) =>
    dispatch(setPreLoadingActionCreator(loading)),
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
