/** @jsx jsx */
import React, { useState } from "react";
import { jsx, css } from "@emotion/core";
import styled, {
  HorizontalMenuContainer as Menu,
  Active,
} from "../theme/styled";
import Styled from "@emotion/styled/macro";
import ContainerSection from "./ContainerSection";
import { Store } from "../../reducers";
import { Project } from "../../reducers/PortfolioInformationReducer";
import { connect } from "react-redux";
import ScrollBar from "../ScrollBar";
import Modal from "./PortfolioSection/Modal";

const MenuItem = styled.li`
  text-align: center;
  float: left;
  width: 25%;
  background: ${({ theme }) => theme.colors.background};
  border-right: 2px solid #fff;
  color: ${({ theme }) => theme.colors.primary};
  padding: 15px 0px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.5s ease-in;
  -webkit-transition: background-color 0.5s ease-in;
`;

const PortfolioContainer = styled.div`
  padding-bottom: 40px;
`;

const List = styled.ul`
  position: relative;
  list-style: none;
  padding-left: 0px;
`;

const Image = Styled.img`
  display: block;
  position: relative;
  width: auto;
  max-width: 100%;
  height: 100%;

  transition: all 0.2s linear;
`;

const Mask = Styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;

  opacity: 0;
  background-color: rgba(12, 19, 27, 0.6);
  transition: all 0.4s ease-in-out;
`;

const Title = Styled.h2`
  text-transform: uppercase;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  color: #fff;
  text-align: center;
  position: relative;
  font-size: 17px;
  padding: 10px;
  margin: 20px 40px 0px 40px;

  transform: translateY(-100px);
  opacity: 0;
  transition: all 0.2s ease-in-out;
`;

const Button = Styled.div`
  display: inline-block;
  text-decoration: none;
  padding: 7px 14px;
  background: #000;
  color: #fff;
  text-transform: uppercase;
  box-shadow: 0 0 1px #000;
  margin-top: 10px;

  opacity: 0;
  transition: all 0.2s ease-in-out;

  :hover {
    box-shadow: 0 0 5px #000;
    color: #212129;
    cursor: pointer;
  }
`;

const Item = styled.li`
  margin: 1px;
  float: left;
  overflow: hidden;
  position: relative;
  text-align: center;
  width: 32.5%;
  height: 150px;
  cursor: default;
  background: #242424 no-repeat center center;

  :hover {
    ${Image} {
      transform: scale(1.1);
    }
    ${Mask} {
      opacity: 1;
    }
    ${Title} {
      opacity: 1;
      transform: translateY(0px);
    }
    ${Button} {
      opacity: 1;
      transform: translateY(0px);
      background-color: ${({ theme }) => theme.colors.darkSecondary};

      :hover {
        background-color: ${({ theme }) => theme.colors.primary};
      }
    }
  }
`;

interface ProjectItemProps {
  imageSrc: string;
  projectTitle: string;
  toggleModal: () => void;
}

const ProjectItem: React.SFC<ProjectItemProps> = ({
  imageSrc,
  projectTitle,
  toggleModal,
}) => {
  return (
    <Item>
      <Image src={imageSrc} />
      <Mask>
        <Title>{projectTitle}</Title>
        <Button onClick={toggleModal}>Read More</Button>
      </Mask>
    </Item>
  );
};

const menuItems = ["Show All", "Commercial", "Personal", "Hobbies"];

export interface PortfolioProps {
  projects: Project[];
}

const PortfolioSection: React.SFC<PortfolioProps> = ({ projects }) => {
  const [menuIndex, setMenuIndex] = useState(0);
  const [showModal, setShowModal] = useState(false);
  const [projectIndex, setProjectIndex] = useState(-1);

  const handleShowModal = (index: number) => {
    setShowModal(true);
    setProjectIndex(index);
  };
  const handleCloseModal = () => {
    setShowModal(false);
  };
  return (
    <ContainerSection>
      <Menu>
        {menuItems.map((item, i) => (
          <MenuItem
            key={item}
            css={menuIndex === i && Active}
            onClick={() => setMenuIndex(i)}
          >
            {item}
          </MenuItem>
        ))}
      </Menu>
      <ScrollBar height={"360px"}>
        <PortfolioContainer>
          <List>
            {projects.map((project, i) => (
              <ProjectItem
                key={project.name}
                imageSrc={project.imageSrc}
                projectTitle={project.name}
                toggleModal={() => handleShowModal(i)}
              />
            ))}
          </List>
        </PortfolioContainer>
      </ScrollBar>
      {showModal && (
        <Modal project={projects[projectIndex]} closeModal={handleCloseModal} />
      )}
    </ContainerSection>
  );
};

const mapStateToProps = (state: Store) => ({
  projects: state.portfolioInformationStore.projects,
});

export default connect(mapStateToProps)(PortfolioSection);
