/** @jsx jsx */

import React from "react";

import { jsx, css } from "@emotion/core";

import TitleName from "../TitleName";
import TitleRole from "../TitleRole";
import { connect } from "react-redux";
import { Store } from "../../reducers";
import { PortfolioInformationStore } from "../../reducers/PortfolioInformationReducer";
import ShadowImage from "../ShadowImage";
import ProfileImage from "./ProfileImage";
import styled from "../theme/styled";

const StyledNameCard = styled.div`
  font-family: "Oswald", sans-serif;
  position: relative;
  background-color: #666;
  height: 50%;
  height: 300px;
  text-align: center;
  padding: 15px;
  margin: 0px;
  margin-bottom: 15px;
`;

const mainTitle = css`
  margin-bottom: 3px;
`;

const subMainTitle = css`
  margin-top: 0px;
`;

export interface NameCardProps {
  portfolioPersonalInfo: PortfolioInformationStore;
}

const NameCard: React.SFC<NameCardProps> = ({ portfolioPersonalInfo }) => {
  return (
    <StyledNameCard>
      <ShadowImage />
      <TitleName
        customCss={[mainTitle]}
        title={portfolioPersonalInfo.name}
        split
      />
      <TitleRole restCss={[subMainTitle]} roles={portfolioPersonalInfo.roles} />
      <ProfileImage />
    </StyledNameCard>
  );
};

const mapStateToProps = (state: Store) => ({
  portfolioPersonalInfo: state.portfolioInformationStore,
});

export default connect(mapStateToProps)(NameCard);
