import React from "react";
import Title from "./Title";
import styled, { noPadding } from "../../theme/styled";
import ScrollBar from "../../ScrollBar";
import { Store } from "../../../reducers";
import { connect } from "react-redux";
import { Employment } from "../../../reducers/PortfolioInformationReducer";

const EmploymentList = styled.ul`
  ${noPadding}
  position: relative;
`;

const EmploymentItem = styled.li`
  position: relative;
  margin-bottom: 15px;
  background-color: ${({ theme }) => theme.colors.background};
  padding: 15px;
  color: #f7efe2;
`;

const TitleCompany = styled.h4`
  color: #f7efe2;
  font-size: 13px;
  font-weight: 400;
  margin-top: 5px;
  margin-bottom: 5px;
`;

const Year = styled.span`
  color: ${({ theme }) => theme.colors.secondary};
  float: right;
`;

const TitleRole = styled.h5`
  color: ${({ theme }) => theme.colors.primary};
  margin-top: 5px;
  font-weight: bold;
`;

const Description = styled.p`
  margin: 0 0 10px;
  font-size: 12px;
`;

export interface EmploymentContentProps {
  employments: Employment[];
}

const EmploymentContent: React.SFC<EmploymentContentProps> = ({
  employments,
}) => {
  return (
    <>
      <Title name="Employment" />
      <ScrollBar height="300px">
        <EmploymentList>
          {employments.map((info, i) => (
            <EmploymentItem key={i}>
              <TitleCompany>
                {info.companyName} <Year>{info.year}</Year>
              </TitleCompany>
              <TitleRole>{info.role}</TitleRole>
              <Description>{info.description}</Description>
            </EmploymentItem>
          ))}
        </EmploymentList>
      </ScrollBar>
    </>
  );
};

const mapStateToProps = (state: Store) => ({
  employments: state.portfolioInformationStore.employments,
});

export default connect(mapStateToProps)(EmploymentContent);
