/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import ShadowImage from "../ShadowImage";
import { WhiteText } from "../TitleName";
import styled from "../theme/styled";

const Container = styled.div`
  position: relative;
  height: 130px;
  /* background-color: #666; */
  margin-bottom: 15px;
  padding: 15px;
  padding-left: 25px;
  z-index: 100;
`;

const TitleHeader = styled.h2`
  color: ${({ theme }) => theme.colors.primary};
  bottom: 15px;
  position: absolute;
`;

export interface TitleProps {
  title: string;
  split?: boolean;
}

const Title: React.SFC<TitleProps> = ({ title, split }) => {
  //Custom title will have 2 part, first part is normal color, second part will have invert style
  const [customTitle, setCustomTitle] = useState<string[]>([]);

  useEffect(() => {
    if (split) {
      setCustomTitle(title.split("|"));
    }
  }, [title]);

  return (
    <Container>
      <ShadowImage />
      <TitleHeader>
        {split ? (
          <>
            {`${customTitle[0]} `}
            <WhiteText>{customTitle[1]}</WhiteText>
          </>
        ) : (
          title
        )}
      </TitleHeader>
    </Container>
  );
};

export default Title;
