import React from "react";
import styled from "@emotion/styled/macro";
import { Row, Col } from "../../theme/styled";

//scss
import "../../../Styles/circleBar.scss";
import { Skill } from "../../../reducers/PortfolioInformationReducer";

const Container = styled.div`
  ${Row}
`;

const CircleContainer = styled.div`
  ${Col}
  float: left;
  width: 33.33333333%;
`;

const CenterText = styled.div`
  margin-top: -5px;
  width: 100%;
  text-align: center;
`;

const CircleTitle = styled.h5`
  margin: 0px;
  font-weight: 600;
  text-transform: uppercase;
`;

export interface OtherSkillProps {
  skills: Skill[];
}

const OtherSkill: React.SFC<OtherSkillProps> = ({ skills }) => {
  return (
    <Container>
      {skills.map((skill) => (
        <CircleContainer key={skill.title}>
          <div
            className={`circle percentage-${skill.progress.replace("%", "")}`}
          >
            <span>{skill.progress}</span>
            <div className="percentage-bar"></div>
          </div>
          <CenterText>
            <CircleTitle>{skill.title}</CircleTitle>
          </CenterText>
        </CircleContainer>
      ))}
    </Container>
  );
};

export default OtherSkill;
