export enum NonPersistenUIActionTypes {
  SET_PRE_LOADING = "Set the preloading state",
  SET_DISPLAY_PORTFOLIO = "Set the boolean to display portfolio",
  SET_CURRENT_LOCATION_PATH = "Set the current path on browser",
}

export type NonPersistentUIAction =
  | {
      type: NonPersistenUIActionTypes.SET_PRE_LOADING;
      payload: boolean;
    }
  | {
      type: NonPersistenUIActionTypes.SET_DISPLAY_PORTFOLIO;
      payload: boolean;
    }
  | {
      type: NonPersistenUIActionTypes.SET_CURRENT_LOCATION_PATH;
      payload: string;
    };

export interface NonPersistentUIStore {
  preLoading: boolean;
  displayPortfolio: boolean;
  sideNavbarMenuItems: SideNavBarmenuItem[];
  sideNavbarSocialMenuItems: SideNavbarSocialMenuItem[];
  currentLocation: string;
}

export default function reducer(
  state: NonPersistentUIStore = {
    preLoading: nonPersistentUIDefaultState.PRE_LOADING_DEFAULT,
    displayPortfolio: nonPersistentUIDefaultState.DISPLAY_PORTFOLIO_DEFAULT,
    sideNavbarMenuItems: nonPersistentUIDefaultState.SIDE_NAVBAR_MENU_ITEMS,
    sideNavbarSocialMenuItems:
      nonPersistentUIDefaultState.SIDE_NAVBAR_SOCIAL_MENU_ITEMS,
    currentLocation: nonPersistentUIDefaultState.CURRENT_LOCATION,
  },
  action: NonPersistentUIAction
) {
  switch (action.type) {
    case NonPersistenUIActionTypes.SET_PRE_LOADING:
      return { ...state, preLoading: action.payload };
    case NonPersistenUIActionTypes.SET_DISPLAY_PORTFOLIO:
      return { ...state, displayPortfolio: action.payload };
    case NonPersistenUIActionTypes.SET_CURRENT_LOCATION_PATH:
      return { ...state, currentLocation: action.payload };
    default:
      return state;
  }
}
