import * as React from "react";
import styled from "@emotion/styled/macro";

const H3 = styled.h3`
  margin-bottom: 20px;
  text-transform: uppercase;
  font-size: 21px;
`;

export interface TitleProps {
  name: string;
}

const Title: React.SFC<TitleProps> = ({ name }) => {
  return <H3>{name}</H3>;
};

export default Title;
