/** @jsx jsx */
import React, { useEffect, useState, Dispatch } from "react";

import { jsx, css } from "@emotion/core";
import styled from "@emotion/styled/macro";

import frontImage from "../images/front-image.jpg";
import ClickImage from "../images/click.png";

import setDisplayPortfolioActionCreator from "../actionCreators/setDisplayPortfolioActionCreator";
import { connect } from "react-redux";
import ShadowImage from "./ShadowImage";
import TitleName from "./TitleName";
import TitleRole from "./TitleRole";
import { Store } from "../reducers";
import { PortfolioInformationStore } from "../reducers/PortfolioInformationReducer";

const Container = styled.div`
  position: relative;
  width: 550px;
  background-color: #666;
  text-align: right;
  padding: 15px;
  margin: 0px auto;
  cursor: pointer;

  @media (max-width: 767px) {
    width: auto;
    text-align: center;
    margin: 0px 15px;
  }
`;

const FrontPageImage = styled.img`
  width: 100%;
  min-height: 150px;
  background-color: #f4d03f;
  text-align: center;
  position: relative;
`;

const FrontClick = styled.div`
  position: absolute;
  max-width: 37px;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
  bottom: 15px;
  opacity: 0;
  transition: all 0.4s;
`;

const ClickImageButton = styled.img`
  display: block;
  max-width: 100%;
  height: auto;
`;

const PulseAnimation = styled.span`
  background: rgba(0, 0, 0, 0);
  border-radius: 50%;
  height: 14px;
  width: 14px;
  position: absolute;
  margin: 11px 0px 0px -12px;
  -webkit-transform: rotateX(55deg);
  -moz-transform: rotateX(55deg);
  -o-transform: rotateX(55deg);
  -ms-transform: rotateX(55deg);
  transform: rotateX(55deg);
  z-index: -2;
  top: 0px;
  left: 4px;
  ::after {
    content: "";
    border-radius: 50%;
    height: 40px;
    width: 40px;
    position: absolute;
    margin: -13px 0 0 -13px;
    -webkit-animation: pulsate 1s ease-out;
    -moz-animation: pulsate 1s ease-out;
    -o-animation: pulsate 1s ease-out;
    -ms-animation: pulsate 1s ease-out;
    animation: pulsate 1s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    -o-animation-iteration-count: infinite;
    -ms-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0);
    -webkit-box-shadow: 0 0 1px 2px #bdbdbd;
    box-shadow: 0 0 1px 2px #bdbdbd;
    -webkit-animation-delay: 1.1s;
    -moz-animation-delay: 1.1s;
    -o-animation-delay: 1.1s;
    -ms-animation-delay: 1.1s;
    animation-delay: 1.1s;
  }

  @-moz-keyframes pulsate {
    0% {
      -webkit-transform: scale(0.1, 0.1);
      -moz-transform: scale(0.1, 0.1);
      -o-transform: scale(0.1, 0.1);
      -ms-transform: scale(0.1, 0.1);
      transform: scale(0.1, 0.1);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
    50% {
      opacity: 1;
      -ms-filter: none;
      filter: none;
    }
    100% {
      -webkit-transform: scale(1.2, 1.2);
      -moz-transform: scale(1.2, 1.2);
      -o-transform: scale(1.2, 1.2);
      -ms-transform: scale(1.2, 1.2);
      transform: scale(1.2, 1.2);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
  }
  @-webkit-keyframes pulsate {
    0% {
      -webkit-transform: scale(0.1, 0.1);
      -moz-transform: scale(0.1, 0.1);
      -o-transform: scale(0.1, 0.1);
      -ms-transform: scale(0.1, 0.1);
      transform: scale(0.1, 0.1);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
    50% {
      opacity: 1;
      -ms-filter: none;
      filter: none;
    }
    100% {
      -webkit-transform: scale(1.2, 1.2);
      -moz-transform: scale(1.2, 1.2);
      -o-transform: scale(1.2, 1.2);
      -ms-transform: scale(1.2, 1.2);
      transform: scale(1.2, 1.2);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
  }
  @-o-keyframes pulsate {
    0% {
      -webkit-transform: scale(0.1, 0.1);
      -moz-transform: scale(0.1, 0.1);
      -o-transform: scale(0.1, 0.1);
      -ms-transform: scale(0.1, 0.1);
      transform: scale(0.1, 0.1);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
    50% {
      opacity: 1;
      -ms-filter: none;
      filter: none;
    }
    100% {
      -webkit-transform: scale(1.2, 1.2);
      -moz-transform: scale(1.2, 1.2);
      -o-transform: scale(1.2, 1.2);
      -ms-transform: scale(1.2, 1.2);
      transform: scale(1.2, 1.2);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
  }
  @keyframes pulsate {
    0% {
      -webkit-transform: scale(0.1, 0.1);
      -moz-transform: scale(0.1, 0.1);
      -o-transform: scale(0.1, 0.1);
      -ms-transform: scale(0.1, 0.1);
      transform: scale(0.1, 0.1);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
    50% {
      opacity: 1;
      -ms-filter: none;
      filter: none;
    }
    100% {
      -webkit-transform: scale(1.2, 1.2);
      -moz-transform: scale(1.2, 1.2);
      -o-transform: scale(1.2, 1.2);
      -ms-transform: scale(1.2, 1.2);
      transform: scale(1.2, 1.2);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
  }
`;

const subTitle = css`
  margin-top: -10px;
  margin-bottom: 10px;
`;

export interface FrontPageProps {
  portfolioPersonalInfo: PortfolioInformationStore;
  setDisplayPortfolioState: (displayPortfolio: boolean) => void;
}

const FrontPage: React.SFC<FrontPageProps> = ({
  setDisplayPortfolioState,
  portfolioPersonalInfo,
}) => {
  const [showClickImage, setShowClickImage] = useState(false);

  useEffect(() => {
    const showClickImageTimer = setTimeout(() => {
      setShowClickImage(true);
    }, 2500);

    return () => {
      clearTimeout(showClickImageTimer);
    };
  }, []);

  const handleClick = () => {
    setShowClickImage(false);
    setDisplayPortfolioState(true);
  };

  return (
    <Container
      css={css`
        top: 300px;
      `}
      onClick={handleClick}
    >
      <ShadowImage />
      <FrontPageImage src={frontImage} alt="Front-image"></FrontPageImage>
      <TitleName customCss={[]} title={portfolioPersonalInfo.name} split />
      <TitleRole restCss={[subTitle]} roles={portfolioPersonalInfo.roles} />
      <FrontClick
        css={css`
          opacity: ${showClickImage ? 1 : 0};
        `}
      >
        <ClickImageButton src={ClickImage} alt="click-image" />
        <PulseAnimation />
      </FrontClick>
    </Container>
  );
};

const mapStateToProps = (state: Store) => ({
  portfolioPersonalInfo: state.portfolioInformationStore,
});

const mapDispatchToProps = (dispatch: Dispatch<any>) => ({
  setDisplayPortfolioState: (displayPortfolio: boolean) =>
    dispatch(setDisplayPortfolioActionCreator(displayPortfolio)),
});

export default connect(mapStateToProps, mapDispatchToProps)(FrontPage);
