import React from "react";
import styled from "./theme/styled";

const StyledShadowImage = styled.div`
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: ${({ theme }) => theme.colors.background};
  background-size: 100% 100%;
  z-index: 0;
`;

export interface ShadowImageProps {}

const ShadowImage: React.SFC<ShadowImageProps> = () => {
  return <StyledShadowImage />;
};

export default ShadowImage;
