/** @jsx jsx */
import React from "react";
import { jsx, css } from "@emotion/core";

import styled, { Active, HorizontalMenuContainer } from "../../theme/styled";

import SkillImage from "../../../images/skillIcon.png";
import EducationIcon from "../../../images/educationIcon.png";
import EmploymentIcon from "../../../images/employmentIcon.png";

const Item = styled.li`
  width: 33.3%;
  border-right: 3px solid #ffffff;
  margin: 0px;
  padding: 24px 0px 19px;
  float: left;
  text-align: center;
  background-color: ${({ theme }) => theme.colors.background};
  color: ${({ theme }) => theme.colors.primary};
  transition: background-color 0.3s ease-in;
  -webkit-transition: background-color 0.3s ease-in;
  font-size: 16px;
  font-family: Oswald;

  :hover {
    cursor: pointer;
    ${Active};
  }
`;

const images = [SkillImage, EducationIcon, EmploymentIcon];

const baseIcon = (index: number) => css`
  background-image: url(${images[index]});
  background-position: left;
  -webkit-background-size: contain;
  background-size: contain;
  background-repeat: no-repeat;
`;

export interface HorizontalNavBarProps {
  menuIndex: number;
  handleClick: (item: string) => void;
  menu: string[];
}

const HorizontalNavBar: React.SFC<HorizontalNavBarProps> = ({
  menuIndex,
  handleClick,
  menu,
}) => {
  return (
    <HorizontalMenuContainer>
      {menu.map((item, i) => (
        <Item
          css={[baseIcon(i), menuIndex === i && Active]}
          key={i}
          onClick={() => handleClick(item)}
        >
          {item}
        </Item>
      ))}
    </HorizontalMenuContainer>
  );
};

export default HorizontalNavBar;
