/** @jsx jsx */

import React, { useEffect, useState } from "react";
import { jsx, css } from "@emotion/core";

import { Store } from "../../reducers";
import { PortfolioInformationStore } from "../../reducers/PortfolioInformationReducer";
import { connect } from "react-redux";
import PersonalInfoItem from "./PersonalInfoItem";
import { removeStraightLineFromText } from "../utils/utils";
import WaterMark from "./WaterMark";
import ContainerSection from "./ContainerSection";
import styled from "../theme/styled";

const InfoContainer = styled.div`
  font-family: "Open Sans", sans-serif;
`;

const StrongText = styled.div`
  font-family: "Oswald", sans-serif;
  display: inline-block;
  font-size: 22.5px;
  margin-top: 10px;
`;

const HighLightText = styled.span`
  background: ${({ theme }) => theme.colors.primary};

  padding: 2px 5px;
  padding-bottom: 2px;
  color: ${({ theme }) => theme.colors.contrast};
  margin-left: 5px;
`;

const FocusText = styled.span`
  color: #666666;
  font-size: 16px;
  font-weight: 600;
`;

const DescriptionText = styled.p`
  color: #666666;
  font-size: 14px;
  line-height: 1.7em;
  letter-spacing: 0.02em;
  margin: 15px 0px;
  padding-right: 75px;
`;

const PersonalInfoTitle = styled.h3`
  font-size: 22.5px;
  margin-top: 60px;
  margin-bottom: 20px;
`;

const PersonalInfo = styled.ul`
  font-family: "Open Sans", sans-serif;
  list-style: none;
  padding: 0;
`;

const Greeting = styled.div`
  margin: 10px 0px;
`;

export interface HomeProps {
  personalInfo: PortfolioInformationStore;
}

const HomeSection: React.SFC<HomeProps> = ({ personalInfo }) => {
  const [name, setName] = useState("");

  useEffect(() => {
    setName(removeStraightLineFromText(personalInfo.name));
  }, [personalInfo]);

  return (
    <ContainerSection>
      <WaterMark />
      <StrongText>
        <Greeting>
          Hello, I am <HighLightText>{name}</HighLightText>
        </Greeting>
        <InfoContainer>
          <FocusText>
            {personalInfo.roles[1]} {"&"}{" "}
          </FocusText>
          <FocusText>{personalInfo.roles[2]}</FocusText>
          <DescriptionText>
            I have two years experience working in web application industry.I
            have rich experience in the production of scalability ReactJS with
            Redux for modern websites.I am also a gamer aswell ().
          </DescriptionText>
        </InfoContainer>
        <PersonalInfoTitle
          css={css`
            font-size: 21px;
          `}
        >
          Personal Info
        </PersonalInfoTitle>
        <PersonalInfo>
          <PersonalInfoItem itemName="Name" itemDescription={name} />
          <PersonalInfoItem
            itemName="Address"
            itemDescription={personalInfo.address}
          />
          <PersonalInfoItem
            itemName="Email"
            itemDescription={personalInfo.email}
          />
          <PersonalInfoItem
            itemName="Phone"
            itemDescription={personalInfo.phone}
          />
        </PersonalInfo>
      </StrongText>
    </ContainerSection>
  );
};

const mapStateToProps = (state: Store) => ({
  personalInfo: state.portfolioInformationStore,
});

export default connect(mapStateToProps)(HomeSection);
