/** @jsx jsx */
import React, { Fragment, useState } from "react";
import { jsx, css } from "@emotion/core";
import Title from "./Title";
import styled, { noPadding, Active } from "../../theme/styled";
import FontAwesomeIcon from "../../FontAwesomeIcon";
import { Store } from "../../../reducers";
import { connect } from "react-redux";
import { Education } from "../../../reducers/PortfolioInformationReducer";

const EducationList = styled.ul`
  ${noPadding}
`;

const ItemTitle = styled.h4`
  background-color: ${({ theme }) => theme.colors.background};
  padding: 10px;
  font-size: 15px;
  font-weight: 600;
  color: ${({ theme }) => theme.colors.primary};
  cursor: pointer;
`;

const ItemContent = styled.div`
  color: #666;
  padding: 0px 15px;
  height: 0px;
  -webkit-transition: height 0.5s; /* Safari */
  -moz-transition: height 0.5s; /* Mozila */
  -o-transition: height 0.5s; /* Opera */
  transition: height 0.5s;
  overflow: hidden;
`;

const ItemContentTitle = styled.h5`
  font-weight: 700;
`;

const Year = styled.span`
  font-style: italic;
  margin-left: 5px;
`;

const Description = styled.p`
  margin: 15px 0px;
  padding-right: 70px;
`;

const PullRight = css`
  position: relative;
  top: -5px;
  margin-left: 0.3em;
  float: right;
`;

const ActiveList = css`
  height: 110px;
`;

export interface EducationsContentProps {
  educations: Education[];
}

const EducationsContent: React.SFC<EducationsContentProps> = ({
  educations,
}) => {
  const [currentEducationIndex, setCurrentEducationIndex] = useState(-1);

  const handleClick = (currentIndex: number) => {
    if (currentEducationIndex === currentIndex)
      return setCurrentEducationIndex(-1);
    setCurrentEducationIndex(currentIndex);
  };

  return (
    <Fragment>
      <Title name="Educations" />
      <EducationList>
        {educations.map((info, i) => (
          <li key={i}>
            <ItemTitle
              onClick={() => handleClick(i)}
              css={currentEducationIndex === i && Active}
            >
              {info.title}
              <FontAwesomeIcon name="fa-sort-desc" customCss={[PullRight]} />
            </ItemTitle>
            <ItemContent css={currentEducationIndex === i && ActiveList}>
              <ItemContentTitle>
                {info.subTitle} <Year>({info.subYear})</Year>
              </ItemContentTitle>
              <Description>{info.description}</Description>
            </ItemContent>
          </li>
        ))}
      </EducationList>
    </Fragment>
  );
};
const mapStateToProps = (state: Store) => ({
  educations: state.portfolioInformationStore.educations,
});

export default connect(mapStateToProps)(EducationsContent);
