/* eslint-disable react-hooks/exhaustive-deps */
/** @jsx jsx */

import React, { useEffect, useState } from "react";
import { jsx } from "@emotion/core";
import styled from "@emotion/styled/macro";
import { Switch, Route, Redirect } from "react-router-dom";

import Title from "./Title";
import { Store } from "../../reducers";
import { connect } from "react-redux";
import { SideNavBarmenuItem } from "../../reducers/NonPersistentUIReducer";
import HomeSection from "./HomeSection";
import ResumeSection from "./ResumeSection";
import PortfolioSection from "./PortfolioSection";
import ContactSection from "./ContactSection";
import { noPadding } from "../theme/styled";

const Container = styled.div`
  @media (max-width: 767px) {
    padding-left: 15px;
    margin-top: 15px;
  }
`;

export interface MainPageProps {
  sideNavBarItems: SideNavBarmenuItem[];
  currentLocation: string;
}

const MainPage: React.SFC<MainPageProps> = ({
  currentLocation,
  sideNavBarItems,
}) => {
  const [pageTitle, setPageTitle] = useState("");

  useEffect(() => {
    const item = sideNavBarItems.find(
      (item) => item.linkTo === currentLocation
    );

    if (item) setPageTitle(item?.headerTitle);
  }, [currentLocation]);
  return (
    <Container css={noPadding}>
      <Title title={pageTitle} split />

      <Switch>
        <Route path="/home" component={HomeSection} strict />
        <Route path="/resume" component={ResumeSection} strict />
        <Route path="/portfolio" component={PortfolioSection} strict />
        <Route path="/contact" component={ContactSection} strict />
        <Redirect to="/home" />
      </Switch>
    </Container>
  );
};

const mapStateToProps = (state: Store) => ({
  sideNavBarItems: state.nonPersistentUIStore.sideNavbarMenuItems,
  currentLocation: state.nonPersistentUIStore.currentLocation,
});

export default connect(mapStateToProps)(MainPage);
