/**@jsx jsx */
import React, { Dispatch, useEffect } from "react";
import { jsx } from "@emotion/core";
import SideNavBarItem from "./SideNavBarItem";
import { Store } from "../../reducers";
import { connect } from "react-redux";
import { SideNavBarmenuItem } from "../../reducers/NonPersistentUIReducer";
import SideNavBarSocialMenu from "./SideNavBarSocialMenu";
import { withRouter, RouteComponentProps } from "react-router-dom";
import setCurrentPathLocationActionCreator from "../../actionCreators/setCurrentPathLocationActionCreator";
import styled, { noPadding } from "../theme/styled";

const MenuContainer = styled.div`
  position: relative;
  display: block;
  clear: both;
  background-color: ${({ theme }) => theme.colors.primary};
  height: 50%;
  height: 305px;
  text-align: right;
  padding: 15px 0px;
  margin: 0px;
  transform: translate3d(0px, 0px, 0px);
  opacity: 1;

  @media (max-width: 767px) {
    height: 60px;
    padding: 0px;
  }
`;

const NavMenu = styled.ul`
  ${noPadding}
`;

export interface SideNavBarProps extends RouteComponentProps {
  items: SideNavBarmenuItem[];
  setCurrentLocation: (location: string) => void;
}

const SideNavBar: React.SFC<SideNavBarProps> = ({
  items,
  location,
  setCurrentLocation,
}) => {
  useEffect(() => {
    setCurrentLocation(location.pathname);
  }, [location.pathname]);

  return (
    <MenuContainer>
      <NavMenu>
        {items.map((item) => (
          <SideNavBarItem
            key={item.linkTo}
            itemName={item.itemName}
            iconName={item.itemIconName}
            to={item.linkTo}
            active={item.linkTo === location.pathname}
          />
        ))}
      </NavMenu>
      <SideNavBarSocialMenu />
    </MenuContainer>
  );
};

const mapStateToProps = (state: Store) => ({
  items: state.nonPersistentUIStore.sideNavbarMenuItems,
});

const mapDispatchToProps = (dispatch: Dispatch<any>) => ({
  setCurrentLocation: (location: string) =>
    dispatch(setCurrentPathLocationActionCreator(location)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(SideNavBar));
