export interface Skill {
  title: string;
  progress: string;
}

export interface ResumeSkill {
  mainSkills: Skill[];
  otherSkills: Skill[];
}

export interface Education {
  title: string;
  subTitle: string;
  subYear: string;
  description: string;
}

export interface Employment {
  companyName: string;
  year: string;
  role: string;
  description: string;
}

export type ProjectCategory = "Commercial" | "Personal" | "Hobbies";

export interface Project {
  imageSrc: string;
  name: string;
  description: string;
  category: ProjectCategory[];
}

export interface PortfolioInformationDefaultState {
  NAME: string;
  ADDRESS: string;
  EMAIL: string;
  PHONE: string;
  ROLES: string[];
  RESUME_SKILL: ResumeSkill;
  EDUCATIONS: Education[];
  EMPLOYMENTS: Employment[];
  PROJECTS: Project[];
}

