export enum PortfolioInformationAction {}

export type PortfolioInformationActionTypes = {
  type: PortfolioInformationAction;
};

export interface PortfolioInformationStore {
  name: string;
  address: string;
  email: string;
  phone: string;
  roles: string[];
  resumeSkill: ResumeSkill;
  educations: Education[];
  employments: Employment[];
  projects: Project[];
}

export default function reducer(
  state: PortfolioInformationStore = {
    name: portfolioInformationDefaultState.NAME,
    roles: portfolioInformationDefaultState.ROLES,
    address: portfolioInformationDefaultState.ADDRESS,
    email: portfolioInformationDefaultState.EMAIL,
    phone: portfolioInformationDefaultState.PHONE,
    resumeSkill: portfolioInformationDefaultState.RESUME_SKILL,
    educations: portfolioInformationDefaultState.EDUCATIONS,
    employments: portfolioInformationDefaultState.EMPLOYMENTS,
    projects: portfolioInformationDefaultState.PROJECTS,
  },
  action: PortfolioInformationActionTypes
) {
  switch (action.type) {
    default:
      return state;
  }
}
