import React, { useState } from "react";

import ContainerSection from "./ContainerSection";
import HorizontalNavBar from "./ResumeSection/HorizontalNavBar";
import SkillsContent from "./ResumeSection/SkillsContent";
import EducationsContent from "./ResumeSection/EducationsContent";
import EmploymentContent from "./ResumeSection/EmploymentContent";

export interface ResumeProps {}

const menu = ["Skills", "Educations", "Employment"];

const ResumeSection: React.SFC<ResumeProps> = () => {
  const [menuIndex, setMenuIndex] = useState(0);

  const handleClick = (name: string) => {
    setMenuIndex(menu.findIndex((item) => item === name));
  };

  return (
    <ContainerSection>
      <HorizontalNavBar
        handleClick={handleClick}
        menuIndex={menuIndex}
        menu={menu}
      />
      {menuIndex === 0 && <SkillsContent />}
      {menuIndex === 1 && <EducationsContent />}
      {menuIndex === 2 && <EmploymentContent />}
    </ContainerSection>
  );
};

export default ResumeSection;
