import React from "react";
import { motion, Variants } from "framer-motion";

const fadeVariants: Variants = {
  fadeInitial: {
    opacity: 0,
  },
  fadeIn: {
    opacity: 1,
    transition: {
      duration: 0.5,
    },
  },
  fadeOut: {
    opacity: 0,
    transition: {
      delay: 0.1,
      duration: 1,
    },
  },
};

export interface FadeAnimateWrapperProps {}

const FadeAnimateWrapper: React.SFC<FadeAnimateWrapperProps> = ({
  children,
}) => {
  return (
    <motion.div
      variants={fadeVariants}
      initial="fadeInitial"
      animate="fadeIn"
      exit="fadeOut"
    >
      {children}
    </motion.div>
  );
};

export default FadeAnimateWrapper;
