import React from "react";
import styled from "../theme/styled";
import AvatarImage from "../../images/avatar.png";

const StyledProfileImage = styled.img`
  position: relative;
  width: 140px;
  height: 140px;
  border-radius: 70px;
  float: none;
  overflow: hidden;
  background-color: #fff;
  border: 7px solid ${({ theme }) => theme.colors.primary};
  z-index: 5;
  opacity: 1; /* Modern!Firefox 0.9+, Safari 2?, Chrome any?Opera 9+, IE 9+ */
  -khtml-opacity: 1;
  -moz-opacity: 1; /* Older than Firefox 0.9 */
  filter: alpha(opacity=100); /* This works in IE 8 & 9 too, but also 5, 6, 7 */

  transition: opacity 1s ease-in-out;
  -moz-transition: opacity 1s ease-in-out;
  -webkit-transition: opacity 1s ease-in-out;
`;

export interface ProfileImageProps {}

const ProfileImage: React.SFC<ProfileImageProps> = () => {
  return <StyledProfileImage src={AvatarImage} alt="profile-image" />;
};

export default ProfileImage;
