import { css } from "@emotion/core";
import styled, { CreateStyled } from "@emotion/styled/macro";

export type Theme = {
  colors: {
    primary: string;
    secondary: string;
    darkSecondary: string;
    border: string;
    background: string;
    contrast: string;
  };
};

export const theme: Theme = {
  colors: {
    primary: "#98C6FA",
    secondary: "#d7e5f5",
    darkSecondary: "#c3d8f0",
    border: "#3a7fce",
    // primary: "#c6fa98",
    // secondary: "#effee3",
    // darkSecondary: "#dbfcbd",
    // border: "#7ef415",
    background: "#0e2036",
    contrast: "#111111",
  },
};

export default styled as CreateStyled<Theme>;

export const Col = () => css`
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;

  @media (min-width: 768px) {
    float: left;
  }
`;

export const Row = css`
  margin-right: -15px;
  margin-left: -15px;
`;

export const noPadding = css`
  list-style: none;
  padding-left: 0px;
`;

export const Active = css`
  background-color: ${theme.colors.primary};
  text-decoration: none;
  color: ${theme.colors.background};
`;

export const HorizontalMenuContainer = styled.ul`
  margin: 30px -30px;
  margin-top: 0px;
  padding: 10px 0px;
  display: block;
  min-height: 50px;
  position: relative;
  top: -30px;
  ${noPadding}
`;

export const ActiveStyle = css`
  background-color: ${theme.colors.background};
  border-bottom-color: ${theme.colors.background};
  border-right: none;
  color: ${theme.colors.primary};

  @media (max-width: 767px) {
    border-bottom: 3px solid ${theme.colors.contrast};
    border-right: none;
  }
`;

export const GeneralInput = css`
  box-sizing: border-box;
  height: 60px;
  width: 100%;
  border: 0;
  border-bottom: 4px solid ${theme.colors.border};
  border-radius: 3px;
  outline: none;
  padding: 2.5rem 0 0 1.1rem;
  background-color: ${theme.colors.secondary};
  color: ${theme.colors.contrast};

  font-weight: 600;
  font-size: 1.4rem;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;

  :focus {
    background-color: ${theme.colors.darkSecondary};
  }
`;
