/* eslint-disable react-hooks/exhaustive-deps */
/** @jsx jsx */
import React, { useEffect, useState, Fragment } from "react";
import { jsx, SerializedStyles } from "@emotion/core";
import styled from "./theme/styled";

export const H1 = styled.h1`
  /* color: #f4d03f; */
  color: ${({ theme }) => theme.colors.primary};
  display: inline-block;
  text-transform: uppercase;
  position: relative;
  margin-top: 20px;
`;

export const WhiteText = styled.span`
  color: #ffffff;
`;

export interface TitleNameProps {
  customCss: SerializedStyles[];
  title: string;
  split?: boolean;
}

const TitleName: React.SFC<TitleNameProps> = ({ customCss, title, split }) => {
  //Custom title will have 2 part, first part is normal color, second part will have invert style
  const [customTitle, setCustomTitle] = useState<string[]>([]);

  useEffect(() => {
    if (split) {
      setCustomTitle(title.split("|"));
    }
  }, []);

  return (
    <H1 css={[...customCss]}>
      {split ? (
        <Fragment>
          {customTitle[0]} <WhiteText>{customTitle[1]}</WhiteText>
        </Fragment>
      ) : (
        title
      )}
    </H1>
  );
};

export default TitleName;
