import React from "react";
import styled from "@emotion/styled/macro";
import { noPadding } from "../../theme/styled";
import { Store } from "../../../reducers";
import { ResumeSkill } from "../../../reducers/PortfolioInformationReducer";
import { connect } from "react-redux";
import MainSkill from "./MainSkill";
import OtherSkill from "./OtherSkill";
import Title from "./Title";

const BarSkills = styled.ul`
  ${noPadding}
`;

const OtherSkillContainer = styled.div`
  max-width: 395px;
  padding: 5px;
  padding-left: 0;
`;

export interface SkillsContentProps {
  skill: ResumeSkill;
}

const SkillsContent: React.SFC<SkillsContentProps> = ({ skill }) => {
  return (
    <>
      <Title name="My Skills" />
      <BarSkills>
        {skill.mainSkills.map((skill) => (
          <MainSkill
            name={skill.title}
            width={skill.progress}
            key={skill.title}
          />
        ))}
      </BarSkills>
      <OtherSkillContainer>
        <Title name="Other Skills" />
        <OtherSkill skills={skill.otherSkills} />
      </OtherSkillContainer>
    </>
  );
};

const mapStateToProps = (state: Store) => ({
  skill: state.portfolioInformationStore.resumeSkill,
});

export default connect(mapStateToProps)(SkillsContent);
