import { combineReducers } from "redux";

import nonPersistentUIStore, {
  NonPersistentUIStore,
} from "./NonPersistentUIReducer";
import portfolioInformationStore, {
  PortfolioInformationStore,
} from "./PortfolioInformationReducer";

export interface Store {
  nonPersistentUIStore: NonPersistentUIStore;
  portfolioInformationStore: PortfolioInformationStore;
}

export default combineReducers({
  nonPersistentUIStore,
  portfolioInformationStore,
});
